<template>
    <div class="workPlan" ref="addImageComponent">
        <navigation-bar :pageName="title" @onLeftClick="goBack">
          <slot slot="nav-right" name="right" >
            <div class="nav-right" @click="showFilter = true">
                <div >筛选</div>
                <img :src="filterIcon" alt="">
            </div>
          </slot>
        </navigation-bar>
        <div class="tabs" v-show="titleType == 0">
            <div class="tab" @click="changeStatus(0)">
              <div class="tab-item">
                <span :style="{color: activeTab == 0 ? '#333333' : '#9A9A9A'}">待接单</span>
                <!-- <div class="dot"></div> -->
              </div>
              <div class="border"></div>
              <div class="border-box" v-show="activeTab == 0"></div>
            </div>
            <div class="tab" @click="changeStatus(1)">
              <div class="tab-item">
                <span :style="{color: activeTab == 1 ? '#333333' : '#9A9A9A'}">待处理</span>
                <!-- <div class="dot"></div> -->
              </div>
              <div class="border"></div>
              <div class="border-box" v-show="activeTab == 1"></div>
            </div>
            <div class="tab" @click="changeStatus(2)">
              <div class="tab-item">
                <span :style="{color: activeTab == 2 ? '#333333' : '#9A9A9A'}">待评价</span>
                <!-- <div class="dot"></div> -->
              </div>
              <div class="border-box" v-show="activeTab == 2"></div>
            </div>
        </div>
        <div class="workList" :style="{ top: titleType == 0 ? '2.4rem' : '1.22667rem' }">
          <van-pull-refresh v-model="isRefresh" @refresh="refresh">
            <div class="workContent" v-show="workList.length > 0">
              <van-list
                  v-model="loading"
                  :finished="finished"
                  :finished-text="workList.length > 0 ? '无更多数据...' : ''"
                  :immediate-check="false"
                  @load="loadMore">
                  <div class="item" v-for="(item,index) in workList" :key="index" @click="goDetail(item)">
                      <div class="title">
                          <div class="building">
                            <div class="name">{{item.areaName}} {{item.buildName}} {{item.unitNo ? item.unitNo + "-" : ''}}{{item.roomName}}</div>
                          </div>
                          <div class="fontRed status" v-if="item.status == '0'">待受理</div>
                          <div class="fontGreen status" v-if="item.status == '1'">受理中</div>
                          <div class="fontYellow status" v-if="item.status == '2' && (item.closeType != '1' && item.closeType != '2')">待评价</div>
                          <div class="fontBlack status" v-if="item.status == '2' && item.closeType == '1'">暂时关闭</div>
                          <div class="fontBlack status" v-if="item.status == '2' && item.closeType == '2'">非正常关闭</div>
                          <div class="fontBlack status" v-if="item.status == '3' && item.closeType != '2'">已结束</div>
                          <div class="fontBlack status" v-if="item.status == '3' && item.closeType == '2'">非正常关闭</div>
                      </div>
                      <div class="content">
                          <div class="img-box">
                              <img v-bind:src="(item.picList && item.picList.length > 0) ? item.picList[0] : defaultImg" alt=""  v-on:error.once="showErrorImg($event)">
                          </div>
                          <div class="dis-box">
                              <div class="content-title">
                                <div>#{{item.labelName}}#</div>
                                <img class="content-duban" v-bind:src="dubanImg" v-if="item.userPosition && item.userPosition != 1" alt="">
                              </div>
                              <div class="content-dis">{{item.content}}</div>
                          </div>
                      </div>
                      <div class="bottom">
                        <div :class="item.relationFlag == 1 ? 'status1' : 'status2' " v-if="item.relationFlag == 1 || item.relationFlag == 2">
                          {{item.relationFlag == 1 ? "主工单" : "子工单"}}
                        </div>
                        <div class="time" v-if="item.status != '2' && item.status != '3'">
                          <span v-if=" !item.compLimitTime && item.confLimitTime">确认时限: {{item.confLimitTime}}</span>
                          <span v-if="item.compLimitTime && item.confLimitTime && item.status == '1'">处理时限: {{item.compLimitTime}}</span>
                          <span v-if="item.compLimitTime && item.confLimitTime && item.status == '2'">完成时限: {{item.compLimitTime}}</span>
                        </div>
                      </div>
                  </div>
              </van-list>
            </div>
            <div class="no-list" v-show="workList.length == 0">
              <img :src="noSourceImg" alt="">
              <div>暂无工单</div>
            </div>
            <div class="bottom-box" v-show="workList.length > 0"></div>
          </van-pull-refresh>
        </div>

        <div class="workTabs" :style="background">
            <div class="tab" @click="changeList(0)">
                <img :src="titleType == 0 ? workingActive : workingNormal" />
                <div :class="titleType == 0 ? 'active' : ''">我的待办</div>
            </div>
            <div class="tab" @click="changeList(1)">
                <img :src="titleType == 1 ? workedActive : workedNormal" />
                <div :class="titleType == 1 ? 'active' : ''">我的已办</div>
            </div>
            <div class="tab create" @click="addWorkPlan">
                <img :src="createIcon"/>
                <div class="addBtn">创建工单</div>
            </div>
            <div class="tab" @click="changeList(2)">
                <img :src="titleType == 2 ? entryActive : entryNormal" />
                <div :class="titleType == 2 ? 'active' : ''">我的录入</div>
            </div>
            <div class="tab" @click="changeList(3)">
                <img :src="titleType == 3 ? allActive : allNormal"/>
                <div :class="titleType == 3 ? 'active' : ''">全部工单</div>
            </div>
        </div>
        <van-popup v-model="showFilter" position="bottom">
            <div class="box">
                <div class="cancel" @click="showFilter = false">
                    <img :src="leftIcon" alt="">
                </div>
                <div class="field-box">
                    <van-field
                        readonly
                        clickable
                        label="小区"
                        :value="residential ? residential.AREA_NAME : ''"
                        input-align="right"
                        placeholder="请选择小区"
                        right-icon="arrow"
                        label-class="field-left"
                        @click="showFilterCondition(0)"/>
                    <van-field
                        readonly
                        clickable
                        label="楼栋"
                        :value="building ? building.BUILD_NAME : ''"
                        input-align="right"
                        placeholder="请选择楼栋"
                        right-icon="arrow"
                        label-class="field-left"
                        @click="showFilterCondition(1)"/>
                    <van-field
                        readonly
                        clickable
                        label="房间号"
                        :value="room ? room.UNIN_ROOM_NAME : ''"
                        input-align="right"
                        placeholder="请选择房间号"
                        right-icon="arrow"
                        label-class="field-left"
                        @click="showFilterCondition(2)"/>
                </div>
                <div class="confirm" @click="filterList">确定</div>
            </div>
        </van-popup>
        <van-popup v-model="showCondition" position="bottom">
          <van-picker
            :columns="conditionList"
            show-toolbar
            :title="conditionTitle"
            :value-key="showKey"
            :default-index="defaultIndexList[selectedConditionType]"
            @cancel="showCondition = false"
            @confirm="seletCondition"/>
        </van-popup>

    </div>
</template>

<script>
import {
  getInterWorkingPlanList,
  getWorkedPlanList,
  getWorkPlanList,
  getMineInternalWorkList
} from "../../services/internalWorkPlanService";
import {
  getVillageList,
  getBuildingList,
  getRoomList
} from "../../services/homePageService";
import { store } from "../../utils/store";
import log from "../../utils/log"
import filterIcon from "@/assets/imgs/workPlan/icon_filter.png";
import defaultImg from "@/assets/imgs/workPlan/imgExample.png";
import finishImg from "@/assets/imgs/workPlan/finishLogo.png";
import noSourceImg from "@/assets/imgs/workPlan/noSource.png";

import workingNormal from "@/assets/imgs/workPlan/icon_working_normal.png";
import workingActive from "@/assets/imgs/workPlan/icon_working_active.png";
import workedNormal from "@/assets/imgs/workPlan/icon_worked_normal.png";
import workedActive from "@/assets/imgs/workPlan/icon_worked_active.png";
import entryNormal from "@/assets/imgs/workPlan/icon_entry_normal.png";
import entryActive from "@/assets/imgs/workPlan/icon_entry_active.png";
import allNormal from "@/assets/imgs/workPlan/icon_all_normal.png";
import allActive from "@/assets/imgs/workPlan/icon_all_active.png";

import createIcon from "@/assets/imgs/workPlan/icon_create.png";
import leftIcon from "@/assets/imgs/workPlan/left_black.png";
import tabBg from "@/assets/imgs/workPlan/bg_workList.png";
import dubanImg from "@/assets/imgs/workPlan/duban.png";
import NavigationBar from "../title/NavigationBar";
export default {
  name: "workPlanInside",
  components: { NavigationBar },
  data() {
    return {
      title: "",
      // 标题类型 0我的待办  1我的已办  2业主工单
      titleType: 0,

      titleList: ["我的待办（内部）", "我的已办（内部）", "我的录入（内部）", "全部工单（内部）"],

      // 上拉加载是否处于加载状态
      loading: false,

      // 上拉加载是否加载完成
      finished: false,
      finishedText: "我是有底线的",

      // 工单列表
      workList: [],
      activeTab: 0,
      isRefresh: false,

      // 工单列表方法
      workListFunction: [
        this.getWorkingPlanList,
        this.getWorkedPlanList,
        this.getMyWorkList,
        this.getWorkPlanList
      ],

      // 展示筛选弹窗
      showFilter: false,

      // 选择的条件选择的类型 小区0 楼栋1 房间号2
      selectedConditionType: 0,

      conditionTypeList: ["选择小区", "选择楼栋", "选择房间号"],

      // 展示筛选条件
      showCondition: false,

      // 筛选条件接口
      filterFunction: [
        this.showResidentialCondition,
        this.showBuildingCondition,
        this.showRoomCondition
      ],

      // 要展示的条件列表
      conditionList: [],

      // 条件筛选的列表
      residentialList: [],
      buildingList: [],
      roomList: [],

      // 选择的小区 楼栋 房间号
      residential: null,
      building: null,
      room: null,

      selectResultList: [this.residential, this.building, this.room],

      // picker框需要展示的列表中的哪个字段值
      showKey: "AREA_NAME",
      showKeyList: ["AREA_NAME", "BUILD_NAME", "UNIN_ROOM_NAME"],

      defaultIndexList: [0, 0, 0],

      // 获取接口的请求参数
      requestData: {
        userCode: null,
        areaCode: null,
        status: 0,
        page: 1, //分页页号
        rows: 10 //每页条数
      },
      page: -1,

      // 图片
      filterIcon: filterIcon,
      defaultImg: defaultImg,
      finishImg: finishImg,
      noSourceImg: noSourceImg,
      createIcon: createIcon,
      leftIcon: leftIcon,
      dubanImg: dubanImg,
      background: {
        backgroundImage:
          "url(" + require("../../assets/imgs/workPlan/bg_workList.png") + ") "
      },
      workingNormal: workingNormal,
      workingActive: workingActive,
      workedNormal: workedNormal,
      workedActive: workedActive,
      entryNormal: entryNormal,
      entryActive: entryActive,
      allNormal: allNormal,
      allActive: allActive,
      scrollTop: 0
    };
  },
  created: function() {},
  beforeRouteLeave(to, from, next) {
    this.scrollTop = document.querySelector(".workList").scrollTop;
    next();
  },
  activated() {
    this.requestData.userCode = store.get("userInfo").USER_CODE;
    var reloadInsideWorkListStatus = store.get("reloadInsideWorkListStatus");

    // 如果有这个值就重新刷新
    if(reloadInsideWorkListStatus){
      store.dele("reloadInsideWorkListStatus");
      this.resetData();
      document.querySelector(".workList").scrollTop = 0;
    } else {
      if (this.titleType == 0) {
        this.page = this.requestData.page;
        this.requestData.page = 1;
        if (Math.round(this.scrollTop / 162) > 10) {
            this.requestData.rows = Math.round(this.scrollTop / 162);
        }
        log.i("request all data before scroll to right position." + Math.round(this.scrollTop / 162) + ";page " + this.page);
        this.changeList(0, this.activeTab);
        document.querySelector(".workList").scrollTop = this.scrollTop;
      } else {
        document.querySelector(".workList").scrollTop = this.scrollTop;
      }
    }
  },
  methods: {
    showErrorImg: function(event) {
      event.currentTarget.src = this.defaultImg;
    },

    changeStatus: function(status) {
      this.activeTab = status;
      //this.requestData.status = status;
      var statusListArray = new Array();
      statusListArray[0] = status;
      this.requestData.statusList = statusListArray.toString();
      this.changeList(this.titleType, status);
    },

    refresh: function() {
      this.isRefresh = true;
      this.changeList(this.titleType, this.activeTab)
    },

    /**
     * 我的待办、我的已办、我录入的、业主工单点击事件
     */
    changeList: function(type, status) {
      let tempStatus = status;
      document.querySelector(".workList").scrollTop = 0;
      // this.workList = [];
      // this.requestData.status = status || 0;
      var statusListArray = new Array();
      if (status == undefined && type == 0) {
          log.i("reset status back to my-todo-list from other bottom tabs.");
          statusListArray[0] = 0;
          this.requestData.statusList = statusListArray.toString();
          tempStatus = 0;
      } else {
          statusListArray[0] = tempStatus || 0;
          this.requestData.statusList = statusListArray.toString();
          if (type != 0) {
              delete this.requestData.statusList;
          }
      }
      this.activeTab = tempStatus || 0;
      this.titleType = type;
      this.setRequestData(1, null, null, null, tempStatus);

      // 清除筛选的条件
      this.setFilterCondition(null, null, null);
      this.getWorkList();
    },

    /**
     * 获取工单列表
     */
    getWorkList: function() {
      this.title = this.titleList[this.titleType];
      this.workListFunction[this.titleType]();
    },

    /**
     * 获取我的待办工单列表
     */
    getWorkingPlanList: function() {
      this.loading = true;
      delete this.areaCodeList;
      getInterWorkingPlanList(this.requestData).then(
        res => {
          if (res.resultCode == 0) {
            this.setWorkList(res);
          } else {
            this.setErrorInfo(res.msg);
          }
        },
        error => {
          this.setErrorInfo(error);
        }
      );
    },

    /**
     * 获取我的已办工单列表
     */
    getWorkedPlanList: function() {
      this.loading = true;
      var doneTaskArray = new Array();
      doneTaskArray[0] = 1;
      doneTaskArray[1] = 2;
      doneTaskArray[2] = 3;
      this.requestData.statusList = doneTaskArray.toString();
      delete this.areaCodeList;
      getWorkedPlanList(this.requestData).then(
        res => {
          if (res.resultCode == 0) {
            this.setWorkList(res);
          } else {
            this.setErrorInfo(res.msg);
          }
        },
        error => {
          this.setErrorInfo(error);
        }
      );
    },

    /**
     * 获取我录入的工单列表
     */
    getMyWorkList: function() {
      this.loading = true;
      var data = Object.assign({ order: "asc", sort: "name"}, this.requestData);
      /*var data = Object.assign({ order: "asc", sort: "name"}, {
          userCode: store.get("userInfo").USER_CODE,
          areaCode: store.get("userInfo").AREA_CODE,
          page: 1, //分页页号
          rows: 10 //每页条数
      });*/
      // delete data.areaCode;
      delete data.status;
      delete this.requestData.statusList;
      getMineInternalWorkList(data).then(
        res => {
          if (res.resultCode == 0) {
            this.setWorkList(res);
          } else {
            this.setErrorInfo(res.msg);
          }
        },
        error => {
          this.setErrorInfo(error);
        }
      );
    },

    /**
     * 获取业主工单列表
     */
    getWorkPlanList: function() {
      this.loading = true;
      delete this.requestData.statusList;
      delete this.areaCodeList;
      getWorkPlanList(this.requestData).then(
        res => {
          if (res.resultCode == 0) {
            this.setWorkList(res);
          } else {
            this.setErrorInfo(res.msg);
          }
        },
        error => {
          this.setErrorInfo(error);
        }
      );
    },

    /**
     * 设置展示的工单列表信息
     */
    setWorkList(res) {
      this.loading = false;
      this.isRefresh = false;
      if (res.data) {
        this.finished = res.data.isLastPage;
        if (this.requestData.page == 1) {
          this.workList = res.data.list ? res.data.list : [];
        } else {
          this.workList = this.workList.concat(
            res.data.list ? res.data.list : []
          );
        }
        if (this.requestData.page == 1 && res.data.isLastPage) {
          this.finished = true;
          this.finishedText = "";
        }
      } else {
        this.finished = true;
      }
      if (this.finished == true && this.page != -1) {
          log.i("request all data before scroll to right position and reset the request data info.");
          this.requestData.page = this.page;
          this.page = -1;
          this.requestData.ROW = 10;
      }
    },

    /**
     * 设置错误的处理信息
     */
    setErrorInfo(error) {
      this.loading = false;
      this.isRefresh = false;
      this.workList = [];
      this.$toast(error);
      this.finished = true;
      this.finishedText = "";
    },

    /**
     * 上拉加载
     */
    loadMore: function() {
      this.requestData.page++;
      this.getWorkList();
    },

    /**
     * 页面返回事件
     */
    goBack: function() {
      this.$router.back(-1);
    },

    /**
     * 创建新工单
     */
    addWorkPlan: function() {
      this.$router.push({ name: "workAddInside" });
    },

    /**
     * 跳转到工单详情页面
     */
    goDetail: function(item) {
      store.set("insideWorkDetailComplCode", item.complCode);
      store.set("insideWorkDetailShowBtn", this.titleType);
      this.$router.push({
        name: "workDetailInside"
      });
    },

    /**
     * 展示条件选择框
     */
    showFilterCondition: function(type) {
      if (type == 1 && !this.residential) {
        this.$toast("请选择小区");
        return;
      }
      if (type == 2 && !this.building) {
        this.$toast("请选择楼栋");
        return;
      }
      this.showCondition = true;
      this.filterFunction[type]();
    },

    /**
     * 展示小区的列表
     */
    showResidentialCondition: function() {
      this.selectedConditionType = 0;
      this.showKey = this.showKeyList[0];
      this.conditionList = this.residentialList;
    },

    /**
     * 展示楼座的列表
     */
    showBuildingCondition: function() {
      this.selectedConditionType = 1;
      this.showKey = this.showKeyList[1];
      this.conditionList = this.buildingList;
    },

    /**
     * 展示房间的列表
     */
    showRoomCondition: function() {
      this.selectedConditionType = 2;
      this.showKey = this.showKeyList[2];
      this.conditionList = this.roomList;
    },

    /**
     * 获取选择的小区列表
     */
    getResidentialCondition: function() {
      getVillageList({
        data: JSON.stringify({ USER_CODE: this.requestData.userCode })
      }).then(
        res => {
          this.residentialList = res;
        },
        error => {
            this.showErroInfo(error);
        }
      );
    },
      showErroInfo: function(err) {
          this.$toast(err);
      },

    /**
     * 获取选择的楼栋的列表
     */
    getBuildingCondition: function() {
      getBuildingList({
        data: JSON.stringify({ AREA_CODE: this.residential.AREA_CODE })
      }).then(
        res => {
          this.buildingList = res;
        },
        error => {
            this.showErroInfo(error);
        }
      );
    },

    /**
     * 获取选择的房间号的列表
     */
    getRoomCondition: function() {
      getRoomList({
        data: JSON.stringify({ BUILD_CODE: this.building.BUILD_CODE })
      }).then(
        res => {
          this.roomList = res;
        },
        error => {
            this.showErroInfo(error);
        }
      );
    },

    /**
     * 设置筛选条件的值
     */
    setFilterCondition: function(residential, building, room) {
      this.residential = residential;
      this.building = building;
      this.room = room;
    },

    /**
     * 选中条件
     */
    seletCondition: function(item, index) {
      this.showCondition = false;
      if (!item || item.length == 0) {
        return;
      }
      if (this.selectedConditionType == 0) {
        if (!this.residential || item.AREA_CODE != this.residential.AREA_CODE) {
          this.setFilterCondition(item, null, null);
          this.defaultIndexList = [index, 0, 0];
          this.getBuildingCondition();
        }
      } else if (this.selectedConditionType == 1) {
        if (!this.building || item.BUILD_CODE != this.building.BUILD_CODE) {
          this.setFilterCondition(this.residential, item, null);
          this.defaultIndexList = [this.defaultIndexList[0], index, 0];
          this.getRoomCondition();
        }
      } else if (this.selectedConditionType == 2) {
        this.defaultIndexList[2] = index;
        this.room = item;
      }
    },

    /**
     * 确定筛选
     */
    filterList: function() {
      document.querySelector(".workList").scrollTop = 0;
      this.setRequestData(
        1,
        this.residential ? this.residential.AREA_CODE : null,
        this.building ? this.building.BUILD_CODE : null,
        this.room ? this.room.ROOM_CODE : null,
        this.requestData.statusList
      );
      this.showFilter = false;
      this.getWorkList();
    },

    /**
     * 重置请求数据
     */
    setRequestData(page, areaCode, buildCode, roomCode, status) {
      this.requestData.page = 1;
      this.requestData.areaCode = areaCode;
      this.requestData.buildCode = buildCode;
      this.requestData.roomCode = roomCode;
      this.requestData.statusList = status;
      this.requestData.areaCodeList = areaCode;// for my work plan
    },

    /**
     * 重置页面数据
     */
    resetData: function() {
      this.titleType = 0;
      // 重新刷新
      this.workList = [];
      this.setRequestData(1, null, null, null, 0);
      this.setFilterCondition(null, null, null);
      this.loading = false;
      this.finished = false;
      this.activeTab = 0;
      this.showFilter = false;
      this.selectedConditionType = 0;
      this.showCondition = false;
      this.conditionList = [];
      this.residentialList = [];
      this.buildingList = [];
      this.roomList = [];
      this.residential = null;
      this.building = null;
      this.room = null;
      this.showKey = "AREA_NAME";
      this.defaultIndexList = [0, 0, 0];
      this.changeList(0, this.activeTab);
      this.getResidentialCondition();
    }
  },
  computed: {

    /**
     * 获取条件选择框的标题
     */
    conditionTitle: function() {
      return this.conditionTypeList[this.selectedConditionType];
    }
  }
};
</script>
<style scoped src="../../assets/css/field.css">
</style>
<style scoped>
.workPlan {
  width: 100%;
